import { Component, ElementRef, Input, OnDestroy } from 'angular2/core';

import { ListHeader } from '../../../../lib/ListHeader';
import { ListItem } from '../../../../lib/ListItem';

import { MLButtonComponent } from '../../../ui_kit/mlbutton.component';
import { SaltieListComponent } from '../../../ui_kit/saltie-list.component';
import { SaltieHeaderComponent } from '../../../ui_kit/saltie-header.component';
import { SaltieCheckboxComponent } from '../../../ui_kit/saltie-checkbox.component';
import { Router } from 'angular2/router';

import { Traveling, TRAVELING } from '../../services/rest/traveling';
import { RestService } from '../../services/rest.service';

var $ = require('jquery');

function buildTravelListItem(content: string, traveling: Traveling, value: number) {
	let buildTravelItem = $(`
		<div class="row saltie-list-item">
			<div style="position: relative; top: 10px;" class="saltie-checkbox-outer">
	            <span class="icon-check saltie-checkbox">
	            </span>
	        </div>
            <span style="margin-left: 10px;" class="saltie-mllarge saltie-weight-100 saltie-white">
				${content}
			</span>
        </div>
	`)

	buildTravelItem.find('.icon-check').css('visibility', traveling.has(value) ? 'visible' : 'hidden');

	buildTravelItem.on("click", () => {
		traveling.toggle(value);
		buildTravelItem.find('.icon-check').css('visibility',traveling.has(value) ? 'visible': 'hidden');
	});

	return {
		html: buildTravelItem,
		handlers: ["click"]
	};
}

@Component({
    selector: 'whos-traveling',
    styles: [
    	require('./whos-traveling.component.scss').toString()
    ],
    template: require('./whos-traveling.component.html'),
    directives: [MLButtonComponent, SaltieListComponent]
})
export class WhosTravelingComponent implements OnDestroy {
	private header: ListHeader;
	private list: Array<any>;
	private traveling: Traveling;

	constructor(private router: Router, private rest: RestService) {
		this.header = new ListHeader(
			'Cruise Conceirge /Travelers',
			'Who\'s Traveling?',
			'You may select more than one'
		);
		this.traveling = rest.traveling;
		this.list = [
			buildTravelListItem('Just me', this.traveling, TRAVELING.JustMe),
			buildTravelListItem('We\'re a couple', this.traveling, TRAVELING.Couple),
			buildTravelListItem('Us and the kids', this.traveling, TRAVELING.Family),
			buildTravelListItem('Extended family', this.traveling, TRAVELING.ExtendedFamily),
			buildTravelListItem('Group of friends', this.traveling, TRAVELING.GroupOfFriends),
			buildTravelListItem('10+ people', this.traveling, TRAVELING.MoreThan10)
		];
	}

	ngOnDestroy() {
		this.list.forEach((item) => {
			item.handlers.forEach(() => {
				item.html.off();
			});
		})
	}

	regions() {
		this.router.navigate(['RegionTraveling']);
	}
}







