export enum TRAVELING {
	JustMe,
	Couple,
	Family,
	ExtendedFamily,
	GroupOfFriends,
	MoreThan10
}

export class Traveling {
	private traveling: Set<number>;
	constructor() {
		this.traveling = new Set<number>();
	}

	add(travel: number) {
		this.traveling.add(travel);
	}

	delete(travel: number) {
		this.traveling.delete(travel);
	}

	has(travel: number) {
		return this.traveling.has(travel);
	}

	toggle(travel: number) {
		if (this.traveling.has(travel)) {
			this.traveling.delete(travel);
		} else {
			this.traveling.add(travel);
		}
	}
}


